@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --text-color: #333;
  --available-balance: #bb64c8;
  --blue-action: #5acaee;
}

html {
  box-sizing: content-box;
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: 'Open Sans', sans-serif;
  margin: 0;
  color: #333;
}

h2 {
  font-size: 2.4rem;
  font-weight: 600;
  line-height: 1.33;
  margin: 0;
  padding: 0;
}

.navegation-link {
  text-align: end;
  justify-content: end;
  color: #5acaee;
  color: var(--blue-action);
  text-decoration: none;
}

.button__blue {
  background-color: #5acaee;
  background-color: var(--blue-action);
  padding: 8px;
  border-radius: 4px;
}

.wrapper {
  max-width: 375px;
  margin: 0 auto;
}

.welcome {
  height: 127px;
  background: linear-gradient(
    to bottom,
    rgba(34, 168, 210, 0.2),
    rgba(255, 0, 0, 0) 70.71%
  );
}

.welcome__info {
  display: flex;
  align-items: center;
  padding: 24px 0;
  margin: 0 29px;
}

.welcome__info p {
  color: var(--text-color);
  margin: 0 auto;
  font-weight: 600;
}

.welcome__info img {
  width: 24px;
  height: 24px;
  border-radius: 50%;
}

.balance__info {
  width: 300px;
  margin: 0 auto;
  margin-top: -50px;
  padding: 12px;
  border-radius: 4px;
  box-shadow: 0 1px 9px 0 rgba(56, 56, 56, 0.2);
  background: #ffffff;
  background-image: linear-gradient(to bottom, #fefefe, #ffffff);
  overflow: visible;
}

.balance__title {
  font-size: 1.2rem;
  color: var(--text-color);
  font-weight: 600;
  display: inline;
}

.balance__available {
  font-size: 3.4rem;
  color: var(--available-balance);
  font-weight: 600;
  margin: 7px 0 0 0;
  line-height: 40px;
}
.highlight {
  width: 375px;
  position: absolute;
  background-color: #783183;
  margin-top: -20px;
  padding: 40px 0;
  z-index: -1;
}

.highlight__list {
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
  width: 300px;
}

.highlight__item {
  width: 54px;
  display: block;
  text-align: center;
}

.highlight__item i {
  width: 20px;
  height: 20px;
  color: white;
}

.highlight__item p {
  margin-top: -10px;
  display: inline-block;
  font-size: 0.8rem;
  color: white;
}
.payment {
  background-color: rgb(255, 255, 255);
  height: 170px;
  margin-top: 110px;
  margin-bottom: 10px;
}

.payment__card {
  width: 300px;
  border-radius: 4px;
  box-shadow: 0 1px 9px 0 rgba(56, 56, 56, 0.2);
  background-image: linear-gradient(to bottom, #fefefe, #ffffff);
  padding: 12px;
  margin: -20px auto 0 auto;
  position: absolute;
  left: calc(50% - 16rem);
}

.payment__header {
  margin-bottom: 8px;
}

.payment__header_text--title {
  font-size: 1.4rem;
  margin: 0;
  line-height: 1.14;
}

.payment__header_text--big {
  margin: 0;
  font-size: 2.8rem;
  font-weight: 600;
  line-height: 1.43;
}

.payment__header_text--date {
  margin: 4px 0 0 0;
  font-size: 1.8rem;
  line-height: 0.89;
  font-weight: 600;
}

.payment__date-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
}

.payment__date-right-info {
  justify-content: flex-end;
}

.payment__date-right-info a {
  color: white;
  font-size: 1.2rem;
  text-decoration: none;
  line-height: 1.33;
  padding: 8px 16px;
}

.payment__resume {
  display: block;
}

.payment__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.payment__list li {
  display: grid;
  grid-template-columns: 20px 2fr 1fr;
  grid-gap: 10px;
  margin: 5px 0;
}

.payment__list i {
  color: var(--blue-action);
  font-size: 1.4rem;
  text-align: center;
  display: block;
  align-self: center;
}

.payment__list li p {
  margin: 0px;
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.33;
}

.payment__list li span {
  font-size: 1.2rem;
  font-weight: 600;
  text-align: end;
}

.movements__list ul {
  padding: 0;
  margin: 0;
}

.movements__list li {
  display: grid;
  grid-template-columns: 30px 2fr 1fr;
  grid-gap: 10px;
  margin: 5px 0;
  vertical-align: middle;
  padding: 8px 24px;
  border-bottom: 1px solid #bbb;
}

.movements__list li img {
  width: 30px;
  height: 30px;
  align-self: center;
}

.movement--title {
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.43;
}

.movement--description {
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.67;
}

.movement--date {
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 1.67;
  text-align: end;
}

.movement--money {
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.43;
  text-align: end;
}
.movements {
  margin-bottom: 110px;
}

.movements__header {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 19px;
  padding: 0 24px;
}
nav {
  height: 82px;
  width: 334px;
  margin: 0 auto;
  padding: 8px 20px 0 21px;
  border-radius: 10px;
  border-top: solid 1px rgba(9, 22, 31, 0.3);
  background-color: #fff;
  position: fixed;
  bottom: 0px;
}

nav ul {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  list-style: none;
}

nav li.active {
  font-size: 2.4rem;
  color: var(--blue-action);
}

.wrapper a {
  text-decoration: none;
}

.disposition__header {
  margin-bottom: 50px;
}

.disposition__navigation {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding: 24px;
}

.disposition__title {
  padding: 24px;
}
.blue-list {
  padding: 0 24px;
  margin-top: 24px;
}

.blue-list__item {
  border-radius: 4px;
  border: solid 2px var(--blue-action);
  margin: 12px 0;
  padding: 10px 18px;
  line-height: 1.43;
  font-size: 1.4rem;
  font-weight: 600;
}

.loan__container {
  padding-bottom: 20px;
}

.loan__slider {
  position: relative;
}

.loan--title-text {
  font-size: 1.4rem;
  line-height: 25px;
  font-weight: 600;
  padding: 0 25px;
}

.loan__info {
  text-align: center;
  position: absolute;
  left: calc(326px - 50%);
  top: 55px;
}

.circular-slider {
  position: absolute;
  left: calc(275px - 50%);
}

.loan__info p {
  margin: 0;
  font-weight: 600;
  color: #222b45;
}

.loan--month-payment {
  font-size: 1.2rem;
  line-height: 2;
}

.loan--amount {
  font-size: 0rem;
  height: 33px;
}

.loan--months {
  font-size: 1.4rem;
  line-height: 1.71;
}

p.loan--blue-text {
  font-size: 1.2rem;
  line-height: 1.33;
  color: var(--blue-action);
}

.loan__container a.button__blue {
  padding: 14px 0;
  margin: 40px auto;
  display: block;
  text-align: center;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 600;
  color: #fff;
  margin-top: 240px;
}

.back-tealish {
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0) 1%,
    rgba(34, 168, 210, 0.1)
  );
}


